import React, { useState, useEffect } from 'react';
import { HashRouter as Router, Route, Routes, useParams, useNavigate } from 'react-router-dom';
import './css/Main.css';
import TouchInfo from './components/TouchInfo';
import GoodsList from './components/GoodsList';
import Detail from './Detail';
import None from './None';

const DetailWrapper = () => {
  const { id } = useParams();
  return <Detail productId={id} />;
};

const Main = () => {
  const [selectedProductId, setSelectedProductId] = useState(null);
  const navigate = useNavigate();

  const handleImageClick = async (id) => {
    setSelectedProductId(id);

    try {
      const response = await fetch(`http://127.0.0.1:5001/goods/${id}`);
      const data = await response.json();

      if (data.SOFTNESS === null && data.THICKNESS === null && data.SMOOTHNESS === null && data.FLEXIBILITY === null) {
        navigate('/none');
      } else {
        navigate(`/detail/${id}`);
      }
    } catch (error) {
      console.error("Error fetching product:", error);
    }
  };

  return (
    <Router>
      <div className="popup-container">
        <Routes>
          <Route
            path="/"
            element={
              <>
                <GoodsList onImageClick={handleImageClick} />
              </>
            }
          />
          <Route path="/detail/:id" element={<DetailWrapper />} />
        </Routes>
      </div>
    </Router>
  );
};

export default Main;
